<template>
  <div>
    <ul class="block-icon-list clearfix">
      <li v-for="item in list"
          :key="item">
        <div class="icon-twrap"
             v-html="item">
        </div>
        <span class="icon-name"
              title="icon-kfckfc">
          <span>icon-kfckfc</span>
        </span>
        <div class="icon-cover">
          <span title="添加入库"
                class="cover-item iconfont cover-item-line icon-gouwuche1">
          </span>
          <span class="cover-item iconfont cover-item-line icon-shoucang1">
          </span>
          <span title="下载"
                @click="downSvg(item)"
                class="cover-item iconfont cover-item-line icon-xiazai">
          </span>
        </div>
      </li>
    </ul>

  </div>
</template>
<script setup>
  import { reactive, toRefs } from "vue"
  //接收浏览器页面消息
  const state = reactive({
    list: []
  })
  const {
    list
  } = toRefs(state)
  chrome.runtime.onMessage.addListener(msg => {
    state.list = msg.data;
  });
  //下载svg
  const downSvg = (data) => {
    var blob = new Blob([data], { type: 'image/svg+xml' });
    //将请求的blob数据转为可下载的url地址
    let url = URL.createObjectURL('<?xml version="1.0" standalone="no"?><!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd">' + blob)
    // 创建一个下载标签<a>
    const aLink = document.createElement('a')
    aLink.href = url
    // 2.直接使用自定义文件名,设置下载文件名称
    aLink.setAttribute('download', 'dome.svg')
    document.body.appendChild(aLink)
    // 模拟点击下载
    aLink.click()
    // 移除改下载标签
    document.body.removeChild(aLink);
  }
  //svg 转png
  const downPng = () => {

  }


</script>
